import Network from '../utils/network.js'
export default {
	getList(data,success,fail) {
		Network.setGetMessage('shipping',data,'', function (res) {
		  success(res)
		}, function (res) {
		  uni.showToast({
			title: res.message,
			icon: 'none',
			duration: 2000
		  })
		})
	},
	getOne(data,success,fail) {
		Network.setPostMessage('shippingOne',data,'', function (res) {
		  success(res)
		}, function (res) {
		  uni.showToast({
			title: res.message,
			icon: 'none',
			duration: 2000
		  })
		})
	},
	getDetails(id, data,success,fail) {
		Network.setGetMessage('shipping/' + id,data,'', function (res) {
		  success(res)
		}, function (res) {
		  uni.showToast({
			title: res.message,
			icon: 'none',
			duration: 2000
		  })
		})
	},
	createSubmit(data,success,fail) {
		Network.setPostMessage('shipping',data,'', function (res) {
		  success(res)
		}, function (res) {
		  uni.showToast({
			title: res.message,
			icon: 'none',
			duration: 2000
		  })
		})
	},
	updateSubmit(data,success,fail) {
		Network.setPostMessage('shipping/' + data.id,data,'', function (res) {
		  success(res)
		}, function (res) {
		  uni.showToast({
			title: res.message,
			icon: 'none',
			duration: 2000
		  })
		})
	},
	deleteSubmit(id,success,fail){
		Network.setPostMessage('shippingDelete/' + id,{},'', function (res) {
		  success(res)
		}, function (res) {
		  uni.showToast({
			title: res.message,
			icon: 'none',
			duration: 2000
		  })
		})
	},
	checkSubmit(data,success,fail) {
		Network.setPostMessage('shippingCheck',data,'', function (res) {
		  success(res)
		}, function (res) {
		  uni.showToast({
			title: res.message,
			icon: 'none',
			duration: 2000
		  })
		})
	},
};
