//URL
function getUrlKey(name){
	return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [, ""])[1].replace(/\+/g, '%20')) || null
}

// 
function getLogin() {
	let that = this;
	let applySecret = uni.getStorageSync('applyDsshopSecret');
	uni.login({
		success(res) {
			if (res.code) {
				uni.request({
					url: applySecret.host + 'wxlogin',
					data: {
						code: res.code
					},
					method: 'POST',
					header: {
						'Content-Type': 'application/json',
						'apply-secret': uni.getStorageSync('applyDsshopSecret').secret,

						openid: uni.getStorageSync('applyDsshopOpenid')

					},
					success: res => {
						if (res.statusCode === 200) {
							if (res.data.openid) {
								uni.setStorageSync('applyDsshopSession_key', res.data.session_key);
								uni.setStorageSync('applyDsshopOpenid', res.data.openid);
							}
						} else {
							uni.showToast({
								icon: 'none',
								title: res.data.message,
								duration: 2000
							});
						}
					},
					fail: res => {
						uni.showToast({
							title: '',
							duration: 2000
						});
					}
				});
			} else {
				console.log('');
			}
		}
	});
}

// 
// url:url
// method: HTTP 
// params:
// header: header
// message:
// success:
// fail
function requestLoading(url, method, params, header, message, success, fail) {
  // console.log(params)
  
  uni.showNavigationBarLoading()
  
  if (message != "") {
    uni.showLoading({
      title: message,
    })
  }

  let applytoken = uni.getStorageSync('dsshopApplytoken')






  
  
  uni.request({
    url: uni.getStorageSync('applyDsshopSecret').host + url,
    data: params,
    header: header ? header : {
      //'Content-Type': 'application/x-www-form-urlencoded'
      'content-type': 'application/json',
      'apply-secret': uni.getStorageSync('applyDsshopSecret').secret,
      'openid': uni.getStorageSync('applyDsshopOpenid'),
	  'Authorization': 'Bearer ' + applytoken
    },
    method: method ? method : 'get',
    success: function (res) {
      //console.log(res.data)
      uni.hideNavigationBarLoading()
      if (message != "") {
        uni.hideLoading()
      }
      if (res.statusCode == 200) {
        success(res.data)
	  }else if (res.statusCode == 500) {
		  fail({message: ''})
		  getLogin()
	  }else if (res.statusCode == 302) {
		  fail({message: ''})
		  getLogin()
	  }else if (res.statusCode == 401) {
		  fail({message: res.data.message})
      } else {
        fail({message: ''})
      }

    },
    fail: function (res) {
      uni.hideNavigationBarLoading()
      if (message != "") {
        uni.hideLoading()
      }
      fail({message: ''})
    },
    complete: function (res) {
		
    },
  })
}
module.exports = {
  request: request,
  setGet: setGet,
  setGetMessage: setGetMessage,
  setPost: setPost,
  setPostMessage: setPostMessage,
  setPut: setPut,
  setPutMessage: setPutMessage,
  setDelete: setDelete,
  setDeleteMessage: setDeleteMessage,
  requestLoading: requestLoading
}